@charset "UTF-8";

header.header {
   background-color: rgb(47, 85, 85);
   color: white;
}

div.container {
   max-width: 1240px;
   padding: 0 2rem;
   margin: 0 auto;

   div:first-child {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }

   div:last-child {
      display: flex;
      align-items: center;
      gap: 2rem;
      transform: translateY(50%)
   }
}
